import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { useFormik } from "formik";
import * as Yup from "yup";
import { connect } from "react-redux";
import { FormattedMessage, injectIntl } from "react-intl";
import * as auth from "../_redux/authRedux";
import { login } from "../_redux/authCrud";
import {
  FetchLoginIncidents,
  clearLoginIncidents
} from "../../../../redux/actions/LoginAction";
import { useSelector, useDispatch } from 'react-redux';
import { FormatListBulletedTwoTone } from "@material-ui/icons";


/*
  INTL (i18n) docs:
  https://github.com/formatjs/react-intl/blob/master/docs/Components.md#formattedmessage
*/

/*
  Formik+YUP:
  https://jaredpalmer.com/formik/docs/tutorial#getfieldprops
*/


function Login(props) {
  const { intl, loginData } = props;
//   message: "Login success"
// status: true
// user: {name: 'Admin', email: 'admin@mfilterit.com', role: 'admin', id: 'ckz2gdafy06721ofa0fwr3h92', phoneNumber: null}
// [[Prototype]]: Object


const loginMessage = loginData && loginData.message ? loginData.message : ''
const loginStatus = loginData && loginData.status ? loginData.status : ''
const userDetails = loginData && loginData.user ? loginData.user : {}
const loginCode = loginData && loginData.code ? loginData.code : 0

const loginErrCode = loginData && loginData.error && loginData.error.code ? loginData.error.code : 0
const loginErrMsg = loginData && loginData.error && loginData.error.message ? loginData.error.message : ''



  const [loading, setLoading] = useState(false)
  const [invalidCred, setInvalidCred] = useState(false)
  const dispatch = useDispatch();
  // const initialValues = {
  //   email: "admin@mfilterit.com",
  //   password: "mfilterit@netad",
  // };

  const initialValues = {
    email: "",
    password: "",
  };

  const LoginSchema = Yup.object().shape({
    email: Yup.string()
      .email("Wrong email format")
      .min(3, "Minimum 3 symbols")
      .max(50, "Maximum 50 symbols")
      .required(
        intl.formatMessage({
          id: "AUTH.VALIDATION.REQUIRED_FIELD",
        })
      ),
    password: Yup.string()
      .min(3, "Minimum 3 symbols")
      .max(50, "Maximum 50 symbols")
      .required(
        intl.formatMessage({
          id: "AUTH.VALIDATION.REQUIRED_FIELD",
        })
      ),
  });

  const enableLoading = () => {
    setLoading(true);
  };

  const disableLoading = () => {
    setLoading(false);
  };

  const getInputClasses = (fieldname) => {
    if (formik.touched[fieldname] && formik.errors[fieldname]) {
      return "is-invalid";
    }

    if (formik.touched[fieldname] && !formik.errors[fieldname]) {
      return "is-valid";
    }

    return "";
  };




  useEffect(() => {
    if (loginStatus && loginMessage === 'Login success') {
      localStorage.setItem('auth_data', JSON.stringify(userDetails))
      // localStorage.removeItem("dpackage")

      // dispatch(FetchMenulist());
      
     window.location = '/incident-dashboard'
      // dispatch(clearLoginIncidents())
    }
  }, [loginStatus]);


//   error: {code: 1008, message: 'login attempt failed'}
// status: false

  useEffect(() => {
    if (loginMessage === "User not found") {
      setInvalidCred(loginMessage)
    } else {
      setInvalidCred(loginErrMsg)
    }
    // dispatch(clearLoginIncidents())
  }, [ loginCode, loginErrCode]);

  const formik = useFormik({
    initialValues,
    validationSchema: LoginSchema,
    onSubmit: ( values, { setStatus, setSubmitting }) => {
      enableLoading();
      setTimeout(() => {
        dispatch(FetchLoginIncidents(values))
        disableLoading();
        setSubmitting(false);
        console.log(values);

        // login(values.email, values.password)
        //   .then(({ data: { authToken } }) => {
        //     disableLoading();

        //     dispatch(FetchLoginIncidents(initialValues))

        //     props.login(authToken);
        //   })
        //   .catch(() => {
        //     setStatus(
        //       intl.formatMessage({
        //         id: "AUTH.VALIDATION.INVALID_LOGIN",
        //       })
        //     );
        //   })
        //   .finally(() => {
        //     dispatch(FetchLoginIncidents(initialValues))
        //     disableLoading();
        //     setSubmitting(false);
        //   });
      }, 1000);
    },
  });
  // const loginData = useSelector(state => state.login.login_data)


  return (
    <div className="login-form login-signin" id="kt_login_signin_form">
      {/* begin::Head */}
      <div className="text-center mb-10 mb-lg-20">
        <h3 className="font-size-h1">
          <FormattedMessage id="AUTH.LOGIN.TITLE" />
        </h3>
        <p className="text-muted font-weight-bold">
          Enter your username and password
        </p>
      </div>
      {/* end::Head */}

      {/*begin::Form*/}
      <form
        onSubmit={(e) => { e.preventDefault(); formik.handleSubmit(e)}}
        className="form fv-plugins-bootstrap fv-plugins-framework"
      >
        {/* {formik.status ? (
          <div className="mb-10 alert alert-custom alert-light-danger alert-dismissible">
            <div className="alert-text font-weight-bold">{formik.status}</div>
          </div>
        ) : (
          <div className="mb-10 alert alert-custom alert-light-info alert-dismissible">
            <div className="alert-text ">
              Use account <strong>admin@demo.com</strong> and password{" "}
              <strong>demo</strong> to continue.
            </div>
          </div>
        )} */}

        <div className="form-group fv-plugins-icon-container">
          {
            invalidCred ? (
            <h6 className="text-danger mb-5 text-center font-weight-bold">
              {/* Invalid credentials */}
              {invalidCred}
            </h6>
            ) : null
          }
          <input
            placeholder="Email"
            type="email"
            className={`form-control form-control-solid h-auto py-5 px-6 ${getInputClasses(
              "email"
            )}`}
            name="email"
            onKeyUp={()=> {
              setInvalidCred('')
             dispatch(clearLoginIncidents())
            }}
            {...formik.getFieldProps("email")}
          />
          {formik.touched.email && formik.errors.email ? (
            <div className="fv-plugins-message-container">
              <div className="fv-help-block">{formik.errors.email}</div>
            </div>
          ) : null}
        </div>
        <div className="form-group fv-plugins-icon-container">
          <input
            placeholder="Password"
            type="password"
            className={`form-control form-control-solid h-auto py-5 px-6 ${getInputClasses(
              "password"
            )}`}
            onKeyUp={()=> {
              setInvalidCred('')
               dispatch(clearLoginIncidents())
            }}
            name="password"
            {...formik.getFieldProps("password")}
          />
          {formik.touched.password && formik.errors.password ? (
            <div className="fv-plugins-message-container">
              <div className="fv-help-block">{formik.errors.password}</div>
            </div>
          ) : null}
        </div>
        <div className="form-group d-flex flex-wrap justify-content-between align-items-center">
          <button
            id="kt_login_signin_submit"
            type="submit"
            disabled={formik.isSubmitting}
            className={`btn btn-primary font-weight-bold px-9 py-4 my-3 w-100`}
          >
            <span>Sign in</span>
            {loading && <span className="ml-3 spinner spinner-white"></span>}
          </button>
        </div>
      </form>
      {/*end::Form*/}
    </div>
  );
}

const mapStateToProps = state => {
  const { login } = state
  return {
    loginData: login && login.login_data ? login.login_data : {},
  }
}

export default injectIntl(connect(mapStateToProps, auth.actions)(Login));